import React, { useContext } from "react";
import "./budget.scss";
import { H, HDown, LevelContext } from "components/heading";

const formatDollars = new Intl.NumberFormat("en-US", {
  style: "currency",
  currency: "USD",
});

const Budget = ({ data }) => {
  const level = useContext(LevelContext);
  return (
    <HDown>
      <div style={{ marginLeft: `${level * 2}rem` }}>
        {Object.keys(data).map((key) => {
          if (Array.isArray(data[key])) {
            return (
              <div key={key} className="PoliceBudget-lineitem">
                <H>{key}</H>
                <div>{formatDollars.format(data[key][0].total)}</div>
              </div>
            );
          } else {
            return (
              <div key={key}>
                <div className="PoliceBudget-lineitem">
                  <H>{key}</H>
                  <div>{getTotal(data[key])}</div>
                </div>
                <Budget data={data[key]} />
              </div>
            );
          }
        })}
      </div>
    </HDown>
  );
};

export default ({ city, data }) => {
  const total = getTotal(data);
  return (
    <div className="PoliceBudget">
      <div className="PoliceBudget-lineitem">
        <h1>{city}</h1>
        <div>{total}</div>
      </div>
      <Budget data={data} />
    </div>
  );
};

const total = (data) => {
  return Object.values(data).reduce((r, d) => {
    return r + (Array.isArray(d) ? sumRows(d) : total(d));
  }, 0);

  function sumRows(rows) {
    return rows.reduce((r, d) => r + +d.total, 0);
  }
};

const getTotal = (data) => {
  return formatDollars.format(total(data));
};
